@tailwind base;
@tailwind components;
@tailwind utilities;


html {
  scroll-behavior: smooth;
}


:root {
  --backround: #1C1A19;
  --orange: #CD773A
  --white: #ffffff;
  --contrast : #EC4899
  --gradient: linear-gradient(to right, #EC4899, #F87171);
  --textAll : #8D98CD
}

body {
  margin: 0 1em;
  font-family: "Montserrat", sans-serif;
  background-color: var(--backround);
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 /* Adjust placeholder text size */
input::placeholder, textarea::placeholder {
  font-size: 0.8em; /* Change this value to adjust the placeholder text size */
}

.error {
  border: 2px solid #e7195a;
  color: #e7195a
}